@import "/src/assets/styles/abstracts/variables.scss";

// main container

.container-wrapper {
  &.main-layout-container {
    padding: 0;
    // max-width: 1920px; 
    // style story
    .style-story-container {
      display: flex;
      text-align: center;
      li {
        margin-left: 16px;
        width: 25%;
        &:first-child {
          margin-left: 0;
        }
        img {
          width: 100%;
        }
        .title {
          margin-top: 34px;
          display: block;
          @include ellipsis(1);
          @include font(20px, 400, #333, 1);
        }
        .des {
          margin: 15px 50px 37px;
          height: 26px;
          @include font(12px, 400, #666, 1.5);
          word-break: keep-all;
        }
      }
    }
    .banner {
      &__container {
        margin-top: 77px;
        ul {
          @include flex(row, center, center);
          li {
            width: 50%;
            &:first-child {
              text-align: right;
              background-color: #fbeded;
            }
            &:last-child {
              background-color: #ededed;
            }
            a {
              display: inline-block;
              img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
    // whats new
    .whats-new-container {
      max-width: 1300px;
      margin: 100px auto 0;
      // slick slide
      .slick-track {
        display: flex;
      }
      .slick-slide {
        overflow: hidden;
        margin: 0 5px;
      }
      .slick-list {
        margin: 0 -5px;
      }
      .product-carousel {
        .slick-arrow.slick-prev {
          top: 38%;
          left: -8rem;
          width: 70px; 
          height: 96px;
          padding: 20px;
          &:after {
            display: block;
            height: 56px;
            content: "";
            background: url("/assets/images/spr-common.png") 0 0 no-repeat;
            background-position: -510px -31px;
          }
        }
        .slick-arrow.slick-next {
          top: 38%;
          right: -8rem;
          width: 70px;
          height: 96px;
          padding: 20px;
          &:after {
            display: block;
            height: 56px;
            content: "";
            background: url("/assets/images/spr-common.png") 0 0 no-repeat;
            background-position: -540px -31px;
          }
        }
        .title {
          margin-top: 17px;
          font-size: 16px;
          color: #000;
          font-weight: 600;
        }
        .price-area {
          margin: 22px 0 12px 0;
        }
      }
    }
    // beauty pick
    .beauty-pick-container {
      overflow: hidden; 
      max-width: 1920px; 
      margin: 0 auto 120px auto; 
      .left {
        float: left; 
        width: 40%;
      }
      .right {
        float: right; 
        width: 60%;
        .product-inner {
          &:first-child {
            margin-top: 0;
          }
          .text-area {
            .sub-heading {
              height: 16px; 
            }
          }
        }
      }
      &:after {
        clear: both;
      }
    }
    // edirorial
    .edirorial-container {
      margin: 0 5px;
      padding: 80px 0;
      background-color: #000;
      color: #fff;
      ul {
        display: flex; 
        margin: 0 auto; 
        width: 1240px; 
        li {
          margin-left: 30px; 
          width: 25%;
          &:first-child {
            margin-left: 0;
          }
        }
      }
      .txt-area {
        margin-top: 20px;
      }
      .title {
        font-weight: 700;
        font-size: 16px; 
      }
      .des {
        margin-top: 16px;
        color: #a8a8a8;
        @include ellipsis(2);
        font-size: 14px; 
      }
      .view-detail-area {
        margin-top: 24px; 
        text-decoration: underline;
        color: #808080;
      }
    }
    // trend
    .trend-container {
      margin: 100px auto 0 auto;
      max-width: 1580px; 
      width: 1580px; 
      .trend-list {
        display: flex;
        margin-top: 50px; 
        &:first-child {
          margin-top: 0;
        }
      }
      .left {
        margin: 0 30px 0 10px; 
        width: 190px; 
        font-size: 35px; 
        em {
          display: block;
          margin: 20px 0 15px 0;
          font-weight: 200;
          font-style: normal;
          text-align: right;
        }
        a {
          display: flex; 
          align-items: center;
          justify-content: end;
          color: #666;
          font-size: 16px;
          text-align: right;
          .arrow-right-icon {
            margin-top: 4px; 
            margin-left: 5px; 
            display: inline-block;
            width: 0px;
            height: 0px; 
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            border-left: 5px solid #666;
          }
        }
        
      }
    }
    // footer banner
    .footer-banner-container {
      margin: 90px auto 80px;
      ul {
        display: flex;
        justify-content: center;
        margin: 0 auto; 
      }
    }
    // footer info
    .footer-info-container {
      display: flex;
      width: 1240px; 
      margin: 0 auto; 
      margin-top: 80px; 
      .left {
        width: 50%;
        height: 400px;
        // tab-list 
        .tab-list {
          display: flex;
          padding-bottom: 20px; 
          height: 50px;
          border-bottom: 2px solid #000;
          // tab 
          .tabs {
            padding-right: 33px; 
            font-size: 22px;
            color: #a3a3a3;
            text-transform: uppercase;
            cursor: pointer;
            &.active-tabs {
              color: #000;
            }
          }
        }
        // content 
        .tab-content {
          display: none; 
          padding-top: 22px;
          font-size: 14px; 
          &.active-content {
            display: block; 
          }
          ul li {
            margin-bottom: 15px; 
          }
          .maker {
            display: inline-block;
            margin-right: 15px;
            width: 4px; 
            height: 4px; 
            background-color: #000;
            border-radius: 4px;
          }
        }
      }
      .right {
        padding: 25px 60px;
        width: 50%;
        h1 {
          height: 60px;
          font-size: 22px; 
          span {
            display: inline-block;
          }
          .phone {
            margin-left: 40px; 
            font-size: 42px;
          }
        }
        .info {
          margin-bottom: 50px;
          font-size: 14px; 
          line-height: 24px; 
        }
        .btn-wrap {
          display: flex; 
          justify-content: space-between;
          &__left {
            display: flex;
            a {
              @include flex(row, center, center);
              width: 95px; 
              line-height: 36px;
              font-size: 14px; 
              border: 1px solid #000;
              &:first-child {
                margin-right: 9px; 
              }
            }
          }
          &__right {
            @include flex(row, center, center); 
            li {
              .icon {
                position: relative;
                display: inline-block;
                margin-left: 9px; 
                width: 31px;
                height: 31px; 
                background-color: #000;
                border-radius: 26px;
                // after 공통
                &:after {
                  display: block;
                  content: "";
                  position: absolute;
                  top: 0;
                  left: 0;
                  background: url("/assets/images/spr-footer.png") 0 0 no-repeat;
                  background-size: 800%;
                }
                &.facebook-icon {
                  position: relative;
                  &:after {
                    width: 41px;
                    height: 41px;
                    background-position: -1px -67px;
                  }
                }
                &.insta-icon {
                  &:after {
                    width: 41px;
                    height: 41px;
                    background-position: -67px -67px;
                  }
                }
                &.pinter-icon {
                  &:after {
                    width: 41px;
                    height: 41px;
                    background-position: -132px -67px;
                  }
                }
                &.youtube-icon {
                  &:after {
                    width: 41px;
                    height: 41px;
                    background-position: -198px -67px;
                  }
                }
              }
              &:first-child {
                .icon {
                  margin-left: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}
